import React from 'react';
import './index.css';

const Copyright = () => (
  <a
    className="copyright"
    href="https://github.com/vovanr/test-react-redux-weather"
    target="_blank"
    rel="noopener noreferrer"
    >
    <span className="copyright__license">MIT </span>
    {' '}
    <span className="copyright__author">VladimirRodkin</span>
  </a>
);

export default Copyright;



